.fortest {
  padding-top:50px;
}
.navbar-default {
  width: 100%;
  background: #03C4EB;
  border: none;
  border-radius: none;
  margin: 0;
}
.nav-item {
  padding: 15px 10px;
  .nav-link {
    margin: 0;
    padding: 0;
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    position: relative;
  }
  .sub-menu {
    position: absolute;
    text-align: center;
    background: #fff;
    margin-top: 15px;
    transition: 0.3s ease-in-out;
    opacity: 0;
    z-index: 10;
    list-style: none;
    visibility: hidden;
  }
}
.nav-item .sub-menu .fath-one a {
  color: #333131;
  text-decoration: none;
}
.nav-item .sub-menu .fath-one .sub-menu-one .son-one {
  padding: 15px 35px;
  border-bottom: 1px solid #e4d0d0;
}
.nav-item .sub-menu .fath-one .sub-menu-one .son-one .a-one {
  color: #333131;
  &:hover {
    color: #d65679;
  }
}
.nav-item .one-li-sub {
  padding: 0px 35px;
  width: 200px;
}
.nav-item .one-li-sub .fath-one {
  padding: 15px 10px;
  float: none !important;
  border-bottom: 1px solid #b9b6b6;
  position: relative;
}
.nav-item .one-li-sub .fath-one a:hover {
  color: #d65679;
}
.nav-item .one-li-sub .fath-one .sub-menu-one {
  position: absolute;
  background: #ffffff;
  transform: translateX(0);
  width: 100%;
  transition: 0.2s;
  top: 0;
  visibility: hidden;
}
.nav-item .one-li-sub .fath-one:hover .sub-menu-one {
  transform: translateX(100%);
  visibility: initial;
  border-left: 1px solid #b9b6b6;
  border-right: 1px solid #b9b6b6;
}
.nav-item:hover .sub-menu {
  transform: translateY(0px);
  opacity: 1;
  visibility: visible;
}
.navbar-right {
  padding-right: 30px;
}
.navbar-default .navbar-nav>li>a {
  color: #fff;
}
.navbar-default .navbar-brand {
  color: #fff;
}
.login-showform {
  position: relative;
  .closeLogin {
    position: absolute;
    left:calc(95% + 8px);
    top: 0;
    font-size: 30px;
    background: #03C4EB;
    color: #fff;
    padding: 5px 10px;
  }
  a {
    text-decoration: none;
  }
}
.btn-formLogin {
  background: #03C4EB;
  color: #fff;
  padding: 5px 10px;
  border: none;
  font-size: 16px;
  margin-top: 8px;
  outline: none;
}
.tabs-form {
  padding-top: 60px;
}
.tabs-login {
  background: #fff;
  color: #03c4eb;
  padding: 10px;
  position: absolute;
  text-align: center;
  width: 40%;
  right: 50%;
