  list-style: none;
  border: 2px solid #b0b5b8;
  &--selected {
    background: #03c4eb;
    color: #fff;
    border-radius: 5px 5px 0 0;
  }
}
.tabs-signup {
  float: left;
  position: absolute;
  left: 50%;
  background: #fff;
  color: #03c4eb;
  padding: 10px;
  text-align: center;
  width: 40%;
  outline: none;
  border: 2px solid #b0b5b8;
  border-left: none;
  
}
.tabs-login:hover {
  background: #03c4eb;
  color: #fff;
}
.tabs-signup:hover {
  background: #03c4eb;
  color: #fff;
}
.tabpanel-form1 {
  padding-top: 50px;
}
.tabpanel-form2 {
  padding-top: 0;
}
.img-user {
  width: 70%;
  margin: 0 auto;
}
.input100 {
  outline: none;
  color: #000;
  font-size: 18px;
  height: 40px;
  padding: 0 20px;
}
.forgot-pass {
  padding-top: 20px;
}
.forgot-pass a{
  font-size: 18px;
