}
.forgot-pass a:hover{
  color: #03c4eb;
}
.btn-Login {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding-top: 20px;
}
.nav-item  .nav-hide-for-mobile {
  display: none;
}
.login100-form-btn {
  background-color: #03c4eb;
  font-size: 18px;
  color: #fff;
  line-height: 1.2;
  outline: none;
  width: 48%;
  border: none;
  height: 40px;
}
.login100-form-btn:hover {
  background-color: #000;
}
.center-login {
  padding-top: 50px;
}
.sb-menu {
  border: none;
  outline: none;
  background: #000;
  color: #fff;
  padding: 6px 6px 7px 6px;
}
.form-control {
  border-radius: 0px;
}
.navbar {
  border-radius: 0px;
}

.modal2 {
  display: none;
}

.modal2.modal--is-open2 {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(176, 180, 183, 0.8);
}
.modal-message2 {
  background: #f8f8f8;
  box-shadow: 0 0 15px #e4e4ec;
  z-index: 3;
  position: fixed;
  width: 60%;
  height: 80%;
  left: 20%;
  right: 20%;
  top: 10%;
  bottom: 10%;
} 
.btn-closeLogin {
  padding: 7px 12px;
  position: absolute;
  top:-5px;
  outline: none;
  border: none;
  border-radius: 50%;
  background: #fff;
  color: #000;
  right: 0;
  margin-top: 5px;
}
.pad-login {
  padding-top: 40px;
}
.img-users-login {
  margin: 0 auto;
  width: 70%;
}
.input-group {
	position: absolute;
	width: 280px;
	transition: .5s;
}
.input-field {
	width: 100%;
	padding: 10px 0;
	margin: 5px 0;
	border-top: 0;
	border-left: 0;
	border-right: 0;
	border-bottom: 1px solid #999;
	outline: none;
	background: transparent;
}
.submit-btn {
	width: 85%;
	padding: 10px 30px;
	cursor: pointer;
	display: block;
	margin: auto;
	background: linear-gradient(to right, #ff105f, #ffad06);
	border: 0;
	outline: none;
	border-radius: 30px;
}
.check-box {
	margin: 30px 10px 30px 0;
}
.check-box span {
	color: #777;
	font-size: 12px;
	bottom: 68px;
	position: absolute;
}
input[type=checkbox], input[type=radio] {
  margin: 15px 0;
}
@media screen and (min-width: 768px) and (max-width: 992px) { 
  .modal-message2 {
    width: 70%;
    left: 15%;
    right: 15%;
  } 
}
@media screen and (min-width: 768px) and (max-width: 830px) {
  .btn-formLogin{
    font-size: 12px;
  }
  .nav-item .sub-menu .fath-one a {
    font-size: 12px;
  }
  .navbar-default .navbar-nav > li > a {
    font-size: 12px;
  }
  .navbar-default .navbar-brand {
    font-size: 12px;
  }
  .nav-link .cart-item-rs {
    font-size: 12px;
  }
  .nav-active-r {
    margin-top: 3px;
  }
  .navbar-form {
    padding: 0;
    margin: 0;
    border: none;
  }
  .sb-menu {
    margin-top: 9px;
  }
}
.nav-link .cart-item-rs {
  font-size: 18px;
  color: #fff;
}
.nav-link .cart-item-rs:hover {
  color: #333;
}
.cart-for-mobile {
  display: none;
}
@media screen and (max-width: 767px) {
  .navbar-default .navbar-collapse, .navbar-default .navbar-form {
    border: none;
    border-color: #03C4EB;
    margin: 0;
    padding: 0;
  }
  .hide-cart {
    display: none;
  }
  .cart-for-mobile {
    display: block;
    position: absolute;
    top: 8px;
    right: 70px;
    .cart-item-rs {
      font-size: 30px;
    }
  }
  .form-control{
    width: 50%;
    float: left;
  }
  .navbar-form .form-group {
    margin-bottom: 8px !important;
  }
  .navbar-collapse {
    display: none;
  }
  .nav-item .nav-link {
    margin-left: 15px;
  }
  .btn-formLogin { 
    margin-left: 18px;
    margin-top: -20px !important;
  }
  .nav-item {
    padding: 0 !important;
    padding-left: 15px !important;
  }
  .nav-item:hover .sub-menu {
    transform: translateY(0);
    transform: translateX(100px);
    position: absolute;
    top: -75%;
    opacity: 1;
    visibility: visible;
  }
  .nav-item .sub-menu {
    transition: 0s ease-in-out;
  }
  .login-title {
    display: none;
  }
  .img-users-login {
    display: none;
  }
  .modal-message2 {
    width: 80%;
    left: 10%;
    right: 10%;
  } 
  .nav-item  .nav-hide-for-mobile {
    display: block;
  }
  .hide-login-register .btn-formLogin {
    display: none;
  }
  .navbar-header {
    margin-bottom: -7px;
  }
}

.bg-bubbles{
  position: fixed;
  width: auto;
  height: auto;
  top: auto;
  left: auto;
  z-index: 1;
}
.bg-bubbles li{
  position: fixed;
  bottom: -160px;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.15);
  padding: 0;
  list-style: none;
  animation: square 30s infinite;
}

.bg-bubbles :nth-child(1){
  left: 10%;
}
.bg-bubbles :nth-child(2){
  left: 20%;
  width: 100px;
  height: 100px;
  animation-delay: 3s;
  /* toc do ( thoi gian hoam thanh cua mot chu ky bay) */
  animation-duration: 4s;
}
.bg-bubbles :nth-child(3){
  left: 30%;
  width: 70px;
  height: 70px;
  animation-delay:2.5s ;
}
.bg-bubbles :nth-child(4)
{
  left: 30%;
  width: 10px;
  height: 10px;
  animation-delay: 5s;
  animation-duration: 6s;
}
.bg-bubbles :nth-child(5)
{
  left: 60%;
  width: 50px;
  height: 50px;
  animation-delay: 2s;
}
.bg-bubbles :nth-child(6)
{
  left: 80%;
  width: 160px;
  height:160px;
  animation-delay: 2s;
  animation-duration: 9s;
}
.bg-bubbles :nth-child(7)
{
  left: 80%;
  width: 45px;
  height:45px;
  animation-delay: 6s;
}
.bg-bubbles :nth-child(8)
{
  left: 50%;
  width: 80px;
  height:80px;
  animation-delay: 2s;
}
.bg-bubbles :nth-child(9)
{
  left: 50%;
  width: 40px;
  height:40px;
  animation-delay: 5s;
}
.bg-bubbles :nth-child(10)
{
  left: 37%;
  width: 120px;
  height:120px;
  animation-delay: 4s;
}



@keyframes square {
  0% {
      transform: translateY(0);
  }
  100% {
      transform: translateY(-900px) rotate(600deg);
  }
}