$color-blue: #03c4eb;
$padd-all: 30px;
.row-post-product {
    padding-top: $padd-all / 3;
    margin-top: $padd-all;
    border-top: 1px solid #e7e7e7;
    margin-bottom: $padd-all;
}
.img-update {
    margin: 0 auto;
    width: 100%;
    padding-left: $padd-all;
    padding-right: $padd-all;
}
.title-update {
    color: #000;
}
.title-update:hover {
    color: $color-blue;
}
.center-text {
    text-align: center;
}
.title_post_product {
    background: #f9f9ff;
    border-left: 3px solid $color-blue;
    margin-bottom: $padd-all / 3;
  }
  
  .title_post_product h2 {
    color: #000;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    line-height: $padd-all + 10px;
    padding-left: $padd-all / 2;
    margin-bottom: 0px;
  }
.row-post-product .swiper-container {
    .swiper-button-next, .swiper-button-prev {
        opacity: 0 !important;
    }
    .swiper-container:hover .swiper-button-next, .swiper-container:hover .swiper-button-prev {
        opacity: 1 !important;
    }
}
