.card-img-top {
    width: 100%;
}
.card-product-details {
    border: 1px solid #f4f4f4;
    padding: 15px;
    text-align: center;
    margin-bottom: 20px;
}
.img-fluid-details {
    
    width: 70%;
}
.img-details{
    text-align: center;
    padding-top: 20px;
}
.details-product {
    padding-top: 30px;
}
.gifts-range {
    padding-top: 13px;
}
.card-pdshopping {
    border: 1px solid #dbdbdb;
    margin-bottom: 20px;
    padding: 15px;
}
.card-pdshopping:hover .cart-btn-shopping {
    opacity: 1;
    transform: translateX(0);
}
.cart-btn-shopping {
    height: 25px;
    opacity: 0;
    transform: translateX(35px);
    position: absolute;
    right: 20px;
    bottom: 42px;
    outline: none;
    border: none;
    background: #03e9f4;
    color: #fff;
    font-size: 16px;
    &:hover {
        background: #03e9f4;
        color: #050801;
        box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 200px #03e9f4;
    }
}
.align-self-center {
    font-weight: bold;
    font-size: 16px;
}
.img-fluid-detail {
    width: 75%;
    margin: 0 auto;
}
.back-to-shop {
    margin-right: 20px;
    margin-top: 10px;
    outline: none;
    border: none;
    background: #03e9f4;
    color: #fff;
    padding: 10px 15px;
    &:hover {
        background: #03e9f4;
        color: #050801;
        box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 200px #03e9f4;
    }
}
.add-to-cart {
    outline: none;
    border: none;
    background: #03e9f4;
    color: #fff;
    padding: 10px 15px;
    &:hover {
        background: #03e9f4;
        color: #050801;
        box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 200px #03e9f4;
    }
}