$value: 0;
$boxes: 32px;
$transform-all: 100%;
$color-blue-opacity: #5C8DF6;
$animated-3d: preserve-3d;
$animation: 1s linear infinite;
.loading-component {
    margin-top: 160px;
    margin-bottom: 250px;
    .boxes2 {
        height: $boxes;
        position: fixed;
        left: 47%;
        width: $boxes;
        position: relative;
        transform-style: $animated-3d;
        transform-origin: 50% 50%;
        margin-top: $boxes;
        transform: rotateX(60deg) rotateZ(45deg) rotateY(0deg) translateZ(0px);
        .box2 {
            width: $boxes;
            height: $boxes;
            top: $value;
            left: $value;
            position: absolute;
            transform-style: $animated-3d;
            &:nth-child(1) {
                transform: translate($transform-all, $value);
                animation: box1 $animation;
            }
            &:nth-child(2) {
                transform: translate($value, $transform-all);
                animation: box2 $animation;
            }
            &:nth-child(3) {
                transform: translate($transform-all, $transform-all);
                animation: box3 $animation;
            }
            &:nth-child(4) {
                transform: translate($transform-all * 2, $value);
                animation: box4 $animation;
            }
        }
    }
}
.boxes2 .box2 > div {
    $auto: auto;
    background: $color-blue-opacity;

    --translateZ: 15.5px;
    --rotateY: 0deg;
    --rotateX: 0deg;
    position: absolute;
    width: $transform-all;
    height: $transform-all;
    background: $color-blue-opacity;
    top: $auto;
    right: $auto;
    bottom: $auto;
    left: $auto;
    transform: rotateY(var(--rotateY)) rotateX(var(--rotateX)) translateZ(var(--translateZ));
    &:nth-child(1) {
        top: $value;
        left: $value;
        background: $color-blue-opacity;
    }
    &:nth-child(2) {
        background: #145af2;
        right: $value;
        --rotateY: 90deg;
    }
    &:nth-child(3) {
        background: #447cf5;
        --rotateX: -90deg;
    }
    &:nth-child(4) {
        background: #DBE3F4;
        top: $value;
        left: $value;
        --translateZ: -90px;
    }
}
@keyframes box1 {
    0%,
    50% {
        transform: translate($transform-all, $value);
    }
    100% {
        transform: translate($transform-all * 2, $value);
    }
}

@keyframes box2 {
    0%{
        transform: translate($value, $transform-all);
    }
    50% {
        transform: translate($value, $value);
    }
    100% {
        transform: translate($transform-all, $value);
    }
}

@keyframes box3 {
    0%,
    50% {
        transform: translate($transform-all, $transform-all);
    }
    100% {
        transform: translate($value, $transform-all);
    }
}

@keyframes box4 {
    0%{
        transform: translate($transform-all * 2, $value);
    }
    50% {
        transform: translate($transform-all * 2, $transform-all);
    }
    100% {
        transform: translate($transform-all, $transform-all);
    }
}