.desc {
    margin: 60px 0;
    text-align: center;
    a button {
        color: #130d1a;
    }
h2 {
    margin: 0;
    font-size: 26px;
}

button {
    border: 2px solid #130d1a;
    background: transparent;
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 13px;
    font-family: "Days One", sans-serif;
    box-shadow: 4px 4px 0 0px rgba(#130d1a, 0.5);
    position: relative;
    transition: 0.3s;
    outline: none;
    cursor: pointer;
    border-radius: 0 20px;
    overflow: hidden;
    &:after {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 0;
    content: "";
    background: #130d1a;
    transition: 0.3s;
    z-index: -1;
    }
    &:hover {
    color: #fff;
    letter-spacing: 2px;
    &:after {
        height: 100%;
    }
    }
    &:active {
    box-shadow: 0 0 0 0px rgba(#000, 0.5);
    }
  }
}
  