$value: 0;
$color-pink: crimson;
$range: 200px;
.loading-component {
    margin-top: $range - 10px;
    margin-bottom: $range + 20px;
    .wrapper33 {
      margin: auto;
      position: absolute;
      left: calc(50% - 100px);
      max-width: $range * 2;
      .loader-outer {
        position: absolute;
        margin: auto;
        top: $value;
        right: $value;
        bottom: $value;
        left: $value;
        width: $range;
        height: 30px;
        border-radius: 20px;
        border-bottom: 1px solid #303030;
        background: #151515;
        box-shadow: inset 0 0 10px 0 rgba(0, 0, 0, 0.5);
        overflow: hidden;
        z-index: 1;
        .loader-inner {
          position: absolute;
          margin: auto;
          top: $value;
          bottom: $value;
          left: -100px;
          width: auto;
          height: 58px;
          color: $color-pink;
          text-align: center;
          text-shadow: 0 0 20px $color-pink;
          font-size: 60px;
          z-index: 2;
          animation: load 2s infinite linear;
        }
      }
  }
      .h1-loading {
        position: absolute;
        margin: auto;
        top: $range / 2;
        left: $value;
        right: $value;
        bottom: $value;
        width: 85px;
        height: 40px;
        text-transform: uppercase;
        text-align: left;
        text-shadow: 0 0 20px $color-pink;
        letter-spacing: 0.1em;
        font-size: 14px;
        font-weight: lighter;
        color: $color-pink
        span {
          display: none;
        }
        &::after {
          animation: txt 1s infinite;
          content: "";
        }
      }

    }  
  @keyframes load {
    0% {
      left: -100px;
    }
    100% {
      left: $range + 20px;
    }
  }
  
  @keyframes txt {
    0% {
      content: "LOADING";
    }
    35% {
      content: "LOADING.";
    }
    65% {
      content: "LOADING..";
    }
    100% {
      content: "LOADING...";
    }
  }