$value: 0;
$padding-for-all: 200px;
$paddin: 80px;
$postion-abs: absolute;
.hero {
    height: 100vh;
    width: 100%;
    background-image: url(../img/sky.jpg);
    background-size: cover;
    background-position: center;
    position: relative;
    overflow-x: hidden;
}
.highway {
    height: $padding-for-all;
    width: 500%;
    display: block;
    background-image: url(../img/road.jpg);
    position: $postion-abs;
    bottom: $value;
    left: $value;
    right: $value;
    z-index: 1;
    background-repeat: repeat-x;
    animation: highway 5s linear infinite;
}
.city {
    height: $padding-for-all + 50px;
    width: 500%;
    background-image: url(../img/city.png);
    position: $postion-abs;
    bottom: $padding-for-all;
    left: $value;
    right: $value;
    display: block;
    z-index: 1;
    background-repeat: repeat-x;
    animation: city 20s linear infinite;
}
@keyframes highway {
    100% {
        transform: translateX(-3000px);
    }
}
@keyframes city {
    100% {
        transform: translateX(-1400px);
    }
}
.car {
    width: $padding-for-all * 2;
    left: 50%;
    bottom: $padding-for-all / 2;
    transform: translateX(-50%);
    position: $postion-abs;
    z-index: 2;
}
.car img {
    width: 100%;
}
.wheel {
    left: 50%;
    bottom: 178px;
    transform: translateX(-50%);
    position: $postion-abs;
    z-index: 2;
}
.wheel img {
    width: $paddin - 8;
    height: $paddin - 8;
    animation: wheel 1s linear infinite;
}
.back-wheel {
    left: -165px;
    position: $postion-abs;
}
.front-wheel {
    left: $paddin;
    position: $postion-abs;
}

@keyframes wheel{
    100% {
        transform: rotate(360deg);
    }
}
  .text-hero {
   position: relative;

  }
  .text-hero h2 {
    padding-top: 30px;
    position: absolute;
    color: #fff;
    width: 60%;
    left: 30%;
    right: 30%;
    font-size: 80px;
  }
  .text-hero h2:nth-child(1) {
    color: transparent;
    -webkit-text-stroke: 2px rgba(255, 0, 0, 0.8);
  }
  .text-hero h2:nth-child(2) {
    color: rgba(255, 0, 0, 0.8);
    animation: animatess 4s ease-in-out infinite;
  }
  @keyframes animatess {
      0%, 100% {
          clip-path: polygon(0% 45%, 15% 44%, 32% 50%, 54% 60%, 70% 61%, 
          84% 59%, 100% 52%, 100% 100%, 0% 100%);
      }
      50% {
        clip-path: polygon(0% 60%, 16% 65%, 34% 66%, 51% 62%, 67% 50%,
         84% 45%, 100% 46%, 100% 100%, 0% 100%);
    }
  }
  @media screen and (max-width: 768px) {
    @keyframes highway {
        100% {
            transform: translateX(-1000px);
        }
    }
    .text-hero h2 {
        font-size: 30px;
        left: 30%;
        right: 30%;
      }
  }
  @media screen and (min-width: 769px) and (max-width: 992px) {
    .text-hero h2 {
        font-size: 50px;
      }
  }