.rvt-alert-home {
    margin-top: 20px;
}
.rvt-alert2 {
  border: 1px solid #dbdbdb;
  text-align: center;
  padding: 15px;
}
.rvt-alert_dismiss2 {
    position: absolute;
    top: 1px;
    right: 16px;
    background: #fff;
    border: none;
    outline: none;
}
.img-home-alert {
    width: 80%;
    margin: 0 auto;
}
.buy-alert {
    margin-top: 10px;
    position: relative;
    padding: 15px;
    color: #03e9f4;
    text-transform: uppercase;
    transition: 1s;
    border: none;
    outline: none;
    display: inline-block;
    overflow: hidden;
    background: #fff;
    &:hover {
        background: #03e9f4;
        color: #050801;
        box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 200px #03e9f4;
    }
    span {
        display: block;
        position: absolute;
        &:nth-child(1) {
            top: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background: linear-gradient(90deg, transparent, #03e9f4);
            animation: animate1 1s linear infinite;
        }
        &:nth-child(2) {
            top: -100%;
            right: 0;
            width: 2px;
            height: 100%;
            background: linear-gradient(180deg, transparent, #03e9f4);
            animation: animate2 1s linear 0.25s infinite;
        }
        &:nth-child(3) {
            bottom: 0;
            right: -100%;
            width: 100%;
            height: 2px;
            background: linear-gradient(270deg, transparent, #03e9f4);
            animation: animate3 1s linear 0.5s infinite;
        }
        &:nth-child(4) {
            bottom: -100%;
            left: 0;
            width: 2px;
            height: 100%;
            background: linear-gradient(360deg, transparent, #03e9f4);
            animation: animate4 1s linear 0.75s infinite;
        }
    }
}
@keyframes animate1 {
    0% {
        left: -100%;
    }
    50%, 100% {
        left: 100%;
    }
}
@keyframes animate2 {
    0% {
        top: -100%;
    }
    50%, 100% {
        top: 100%;
    }
}
@keyframes animate3 {
    0% {
        right: -100%;
    }
    50%, 100% {
        right: 100%;
    }
}
@keyframes animate4 {
    0% {
        bottom: -100%;
    }
    50%, 100% {
        bottom: 100%;
    }
}
@media screen and (max-width: 991px) {
    .range-home {
        padding-top: 20px;
    }
    .countdown-promotions {
        margin-top: 20px;
    }
    .countdown-timer22 {
        padding-top: 70px !important;
    }
    
}
@media screen and (min-width: 992px) {
    .arcodion-home {
        display: none;
    }
}
@media screen and (max-width:768px) {
    .btn-jean {
        padding-top: 0 !important;
        text-align: center;
    }
    .none-alert-sm {
        display: none;
    }
    .non-alert {
        display: none;
    }
}
@media screen and (min-width:769px) and (max-width: 991px){
    .btn-jean {
        padding-top: 0 !important;
    }
    .none-alert-sm {
        display: none;
    }
}

