$color-white: #fff;
$value: 0;
$transparent: transparent;
$all-padding: 10px;
$color-blue: #3498db;
$none: none;
$color-orange: #ff9727;
.play-icon {
    $kc-main: 35px;

    margin-top: 200px;
    width: $kc-main + 20px;
    height: $kc-main + 20px;
    position: fixed;
    right: $kc-main;
    bottom: $kc-main;
    z-index: 2;
    border-radius: 50%;
    border: 1px solid $color-white;
    background: $color-white;
    font-size: $kc-main;
    animation: key-animation 1s linear infinite;
}
.play-icon-chat {
    $postion: calc(15% + 1px);

    position: absolute;
    top: $postion;
    left: $postion;
    color: rgba(255, 0, 0, 0.8);
}

@keyframes key-animation {
    0% {
        box-shadow: 0 0 0 0 rgba(255, 0, 0, 0.4), 0 0 0 $all-padding rgba(255, 0, 0, 0.4), 0 0 0 20px rgba(255, 0, 0, 0.4);
    }
    100% {
        box-shadow: 0 0 0 $all-padding rgba(255, 0, 0, 0.4), 0 0 0 20px rgba(255, 0, 0, 0.3), 0 0 0 30px rgba(255, 0, 0, 0.2);
    }
}
.backgr-welcome {
  position: fixed;
  top: $value;
  bottom: $value;
  left: $value;
  right: $value;
}
.skeleton-tittle {
  margin-top: $all-padding;
}
.skeleton-loading2 {
  border: 1px solid #bababa;
  padding: $all-padding + 5px;
  text-align: center;
}
.skeleton-title {
  padding: $value;
  margin-top: $all-padding * 2;
}
.skeleton-nav {
  margin-top: -3px;
  padding: $value;
}
.nav-skeleton{
  margin-top: -3px;
}
.css-1vmnjpn-skeletonStyles-Skeleton {
  border-radius: $value !important;
}

.modal {
  display: $none;
}

.modal.modal--is-open {
  display: block;
}
.modal-message {
  background: $color-white;
  position: fixed;
  bottom: $value;
  z-index: 4;
  height: 450px;
  right: $value;
}
.btn-closeMessage {
  padding: $all-padding;
  position: absolute;
  top:$value;
  outline: $none;
  border: $none;
  background: $color-orange;
  color: #000;
  right: $value;
  margin-top: 5px;
}
.img-admin-chat{
  position: relative;
  img{
    border-radius: 50%;
    width: 50px;
    height: 50px;
  }
  &::before {
    position: absolute;
    content: ' ';
    width: $all-padding;
    top: $value;
    left: 10%;
    background: green;
    border-radius: 50%;
  }
}
.popup-chat {
  height: 100%;
  width: 400px;
}
.bg-popup-chat {
  background: $color-orange
}
.online-message {
  position: relative;
  &::before {
    width: 15px;
    background: #34a105;
    border-radius: 50%;
    height: 15px;
    top: -12px;
    right: -45px;
    position: absolute;
    content: '';
  }
}
.contact-admin {
  position: relative;
  &::before {
    top: -35px;
    left: 60px;
    position: absolute;
    content: 'Admin';
  }
}
.app {
  $paddingforApp: 400px;
  width: $paddingforApp;
  height: $paddingforApp;
  display: flex;
  flex-direction: row;
  .contact-list {
    background-color: $color-white;
    width: $paddingforApp / 2;
    overflow: auto;
    .title {
      padding-bottom: $value;
      font-size: 150%;
    }
    ul {
      list-style: $none;     
      li {
        position: relative;
        cursor: pointer;
        transition: all .2s ease-out;
        &.active, &:hover {
          background-color: lighten($color-blue, 40%);
          color: $color-blue;
          border-radius: 6px;
        }
      }
    }
  }
  
  .messages {
    background-color: $color-white;
    border-radius: 6px;
    overflow: hidden;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    .messages-history {
      overflow: auto;
      flex: 1 1 auto;
      display: flex;
      flex-direction: column-reverse;
      align-items: end;
      .message {
        width: 100%;
        padding: 12px;
        padding-top: $value;
        &:last-child {
          padding-top: 12px;
        }
        &.me {
          text-align: right;
          .message-body {
            color: $color-white;
            padding: 5px;
            background-color: $color-blue;
          }
        }
        .message-body {
          background-color: #DDD;
          color: #333;
          font-weight: 400;
          display: inline-block;
          padding: 12px / 1.5 12px;
          border-radius: 6px;
          max-width: 250px;
          min-width: 75px;
          line-height: 20px;
          padding: 5px;
          text-align: initial;
        }
      }
    }
    
    .messages-inputs {
      display: flex;
      input[type="text"] {
        background-color: $transparent;
        border: $none;
        padding: 12px;
        resize: vertical;
        flex: 1 1 auto;
        outline: $none;
      }
      .btn-sendMessage {
        padding: 0 12px;
        background-color: $transparent;
        border: $none;
        outline: $none;
        cursor: pointer;
        border-radius: $none;
        &:hover {
          background: $color-orange
        }
      }
    }
  }
}