$colorF9: #f9f9ff;
$border: 1px solid #c6c3d4;
$padding-all: 10px;
.conty {
    padding-top: 36px;
}
.samsung-detail {
    background: $colorF9;
    border: $border;
}
.samsung-detail2 {
    color: #03c4eb;
    font-size: $padding-all + 6px;
    padding: $padding-all;
    border-bottom: $border;
}
.samsung-detail3 {
    padding-left: $padding-all;
    padding-bottom: $padding-all /2;
}
.samsung-post {
    margin-top: 30px;
    background: $colorF9;
    border: $border;
}
.title_post_samsung {
    background: #f4f4f4;
    border-left: 3px solid #03c4eb;
    margin-bottom: $padding-all;
  }
  
  .title_post_samsung h2 {
    color: #000;
    font-size: 18px;
    font-weight: 500;
    line-height: 40px;
    padding-left: 15px;
    margin-bottom: 0;
  }