.service-about{
    text-align: center;
    color: #03C4EB;
    position: relative;
    &::after {
        position: absolute;
        content: '';
        width: 40%;
        top: calc(50% + 2px);
        margin-left: 20px;
        border: 1px solid #03C4EB;
    }
    &::before {
        position: absolute;
        content: '';
        width: 40%;
        left: 0;
        top: calc(50% + 2px);
        margin-left: 20px;
        border: 1px solid #03C4EB;
    }
}
.title-p {
    text-align: center;
    padding-bottom: 20px;
}
.backgroud-about {
    background: linear-gradient(rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.7)), url("../img/services-bg.jpg") fixed center center;
    background-size: cover;
    padding: 80px 0 60px 0;
}
.service-item {
    margin-bottom: 20px;
}

.service-icon {
    float: left;
    background: #03C4EB;
    padding: 16px;
    border-radius: 50%;
    transition: 0.5s;
    border: 1px solid #03C4EB;
    i {
        color: #fff;
        font-size: 24px;
    }
}
.service-item:hover .service-icon {
    background: #fff;
}
.service-item:hover .service-icon i {
    color: #03C4EB;
}

.service-title {
    margin-left: 80px;
    font-weight: 700;
    margin-bottom: 15px;
    text-transform: uppercase;
    a {
        color: #111;
        text-decoration: none;
    }
}

.service-description {
    margin-left: 80px;
    line-height: 24px;
}
@media screen and (min-width: 769px) and (max-width: 992px) {
    .service-about::before {
      position: absolute;
      content: '';
      width: calc(35% + 5px);
      top: calc(50% + 2px);
      margin-left: 20px;
      border: 1px solid #03C4EB;
    }
  }
  @media screen and (max-width: 768px) {
    .service-about{
      &::after, &::before {
        display: none;
      } 
    }
  }